import React from "react";
import {
  LandingSectionDiv,
  ImageBox,
  InformationStack,
  ScrollLinkText,
  SocialIconLink,
  TypographyTitle,
  StyledBackgroundText,
} from "./styles";
import { FaGithub, FaInstagram, FaXing, FaSpotify } from "react-icons/fa";
import {
  Typography,
  Stack,
  Divider,
  Box,
  Tooltip,
  IconButton,
} from "@mui/material";
import { Link } from "react-scroll/modules";

export const LandingSection = ({ id }) => {
  return (
    <LandingSectionDiv id={id}>
      <StyledBackgroundText>developer</StyledBackgroundText>
      <Stack direction="row" spacing={5}>
        <InformationStack>
          <Stack sx={{ m: 2 }}>
            <Typography variant="h4">Welcome to my website!</Typography>
            <TypographyTitle variant="h1">Elias </TypographyTitle>
            <TypographyTitle variant="h1" bold="true">
              Scharlach
            </TypographyTitle>
            <Typography variant="h5">Software Developer</Typography>
          </Stack>
          <Divider variant="middle" />
          <Box sx={{ m: 2 }}>
            <Stack direction="row" spacing={2}>
              <Link to="About" smooth duration={500}>
                <ScrollLinkText variant="h6">Read about me</ScrollLinkText>
              </Link>
              {/* <Typography variant="h6">or</Typography>
              <Link to="Development" smooth duration={500}>
                <ScrollLinkText variant="h6">View Projects</ScrollLinkText>
              </Link> */}
            </Stack>
            <Stack direction="row" spacing={1}>
              <SocialIconLink
                style={{ backgroundColor: "transparent" }}
                href="https://github.com/EliasSchar"
                target="_blank"
                aria-label="GitHub"
              >
                <FaGithub />
              </SocialIconLink>
              <SocialIconLink
                style={{ backgroundColor: "transparent" }}
                href="https://www.xing.com/profile/Elias_Scharlach"
                target="_blank"
                aria-label="Xing"
              >
                <FaXing />
              </SocialIconLink>
              <SocialIconLink
                style={{ backgroundColor: "transparent" }}
                href="https://www.instagram.com/eliasscharlach/"
                target="_blank"
                aria-label="Instagram"
              >
                <FaInstagram />
              </SocialIconLink>
              <SocialIconLink
                style={{ backgroundColor: "transparent" }}
                href="https://open.spotify.com/user/elias_scharlach"
                target="_blank"
                aria-label="Spotify"
              >
                <FaSpotify />
              </SocialIconLink>
            </Stack>
          </Box>
        </InformationStack>
        <ImageBox
          component="img"
          alt="Picture of the websiteOwner."
          src={require("../../Images/WebsiteOwner.png")}
        ></ImageBox>
      </Stack>
    </LandingSectionDiv>
  );
};
