import { styled } from "@mui/system";
import { Typography } from "@mui/material";

export const IconTypography = styled(Typography)(({ theme }) => ({
  background: "transparent",
  textAlign: "center",
  fontSize: "24px",
  display: "block",
  height: "40px",
  width: "40px",
  borderRadius: "50%",
  border: "2px solid white",
  margin: "4px",
}));

export const IconTypographyMain = styled(Typography)(({ theme }) => ({
  fontWeight: "bold",
  marginTop: "2px",
}));

export const IconTypographySecondary = styled(Typography)(({ theme }) => ({
  fontWeight: "lighter",
}));
