import * as React from "react";
import MenuIcon from "@mui/icons-material/Menu";
import { scroller } from "react-scroll";
import { IconButton, Typography, Toolbar, Menu, Tooltip } from "@mui/material";
import { StyledAppBar } from "./styles";
// import { MaterialUISwitch } from "./styles";
import WebsiteIcon from "../WebsiteIcon";
import MenuItem from "@material-ui/core/MenuItem";

const NavBar = ({ sections, themeToggler }) => {
  const [anchorElMenu, setAnchorElMenu] = React.useState(null);

  const handleOpenMenu = (event) => {
    setAnchorElMenu(event.currentTarget);
  };

  const handleClickCloseMenu = () => {
    setAnchorElMenu(null);
  };
  const handleClickSectionMenu = ({ section }) => {
    setAnchorElMenu(null);
    scroller.scrollTo(section, {
      duration: 800,
      delay: 0,
      smooth: "easeInOutQuart",
    });
  };

  return (
    <>
      <StyledAppBar position="fixed">
        <Toolbar>
          <WebsiteIcon />
          <Typography variant="h6">Menu</Typography>
          {/* <Tooltip title="Open menu"> */}
          <IconButton edge="end" color="inherit" onClick={handleOpenMenu}>
            <MenuIcon />
          </IconButton>
          {/* </Tooltip> */}
          <Menu
            sx={{ mt: "45px" }}
            id="menu-appbar"
            anchorEl={anchorElMenu}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={Boolean(anchorElMenu)}
            onClose={handleClickCloseMenu}
            disableScrollLock
          >
            {sections.map((section) => (
              <MenuItem
                key={section}
                onClick={() => handleClickSectionMenu({ section })}
              >
                <Typography textAlign="center">{section}</Typography>
              </MenuItem>
            ))}
            {/* <MaterialUISwitch onClick={themeToggler} /> */}
          </Menu>
        </Toolbar>
      </StyledAppBar>
    </>
  );
};
export default NavBar;
