import { styled } from "@mui/system";
import { Typography } from "@mui/material";

//defines the container of each section (exception: landingPage)
export const SectionDiv = styled("div")(({ theme }) => ({
  scrollSnapAlign: "start",
  position: "relative",
  // minHeight: "100vh",
  height: "auto",
  marginLeft: "5%",
  marginRight: "5%",
  [theme.breakpoints.down("sm")]: {
    marginLeft: "1%",
    marginRight: "1%",
  },

  // borderBottom: "1px solid red", //TODO remove
}));

export const SectionTitle = styled(Typography)(({ theme }) => ({
  textAlign: "center",
  wordWrap: "break-word",
  marginTop: theme.spacing(10),
  //TODO set color by theme
}));

export const SectionSubtitle = styled(Typography)(({ theme }) => ({
  color: theme.palette.primary.main,
  // textTransform: "uppercase",
  // fontWeight: "bold",
}));

export const InfoTextTypography = styled(Typography)(({ theme }) => ({
  color: "rgba(255, 255, 255, 0.7)", //theme.palette.Text.Secondary,
}));
