import React from "react";
import { Typography, List, ListItem, Divider, Box } from "@mui/material";
import { ContentBox, ImageBox, InformationBox } from "./styles";
import {
  SectionDiv,
  SectionTitle,
  SectionSubtitle,
  InfoTextTypography,
} from "../SectionStyles";

export const AboutSection = ({ id }) => {
  return (
    <SectionDiv id={id}>
      <SectionTitle variant="h2">About Me</SectionTitle>
      <ContentBox>
        <ImageBox
          component="img"
          alt="Picture of the websiteOwner."
          src={require("../../Images/WebsiteOwner_old.png")}
        ></ImageBox>
        <InformationBox>
          <Box sx={{ m: 1 }}>
            <SectionSubtitle gutterBottom variant="h6">
              Who am I?
            </SectionSubtitle>
            <Typography gutterBottom variant="h4">
              I'm Elias Scharlach, a C# developer.
            </Typography>
            <InfoTextTypography gutterBottom variant="body1">
              After my apprenticeship as an IT specialist for application
              development at ORGADATA AG, I am working there as a developer in
              the BIM* area. In addition to my apprenticeship, I started
              studying business informatics and am enrolled at the University of
              Applied Sciences Emden/Leer.
            </InfoTextTypography>
            <InfoTextTypography gutterBottom variant="body1">
              Since 2018, I have been gaining practical experience in software
              development and theoretical knowledge in all areas of computer
              science. In my free time I implement private projects and test new
              technologies. Currently I am engaged in web and app development
              using Angular, React and Flutter.
            </InfoTextTypography>
          </Box>
          <Divider sx={{ borderBottomWidth: 3 }} />
          <Box sx={{ m: 2 }}>
            <SectionSubtitle variant="h6">
              Overview Curriculum Vitae
            </SectionSubtitle>
            <List>
              <ListItem disablePadding>
                <Typography variant="h6">2018</Typography>
                <List>
                  <ListItem>
                    <Typography variant="body2">
                      General University Entrance Qualification
                    </Typography>
                  </ListItem>
                  <ListItem>
                    <Typography variant="body2">
                      Start apprenticeship software developer
                    </Typography>
                  </ListItem>
                  <ListItem>
                    <Typography variant="body2">
                      Start studies business informatics
                    </Typography>
                  </ListItem>
                </List>
              </ListItem>
              <ListItem disablePadding>
                <Typography variant="h6">2020</Typography>
                <List>
                  <ListItem>
                    <Typography variant="body2">
                      Completion of the apprenticeship
                    </Typography>
                  </ListItem>
                </List>
              </ListItem>
            </List>
          </Box>
          <Divider sx={{ borderBottomWidth: 3 }} />
          <InfoTextTypography variant="body2" fontSize={12}>
            * Building Information Modeling (BIM) is the foundation of digital
            transformation in the architecture, engineering, and construction
            industry.
          </InfoTextTypography>
        </InformationBox>
      </ContentBox>
    </SectionDiv>
  );
};
