import React from "react";
import ClipLoader from "react-spinners/ClipLoader";
import { StyledDiv } from "./styles";

export default function Loader({ loading }) {
  return (
    <StyledDiv>
      <ClipLoader
        color={"#FFFF"}
        loading={loading}
        size={50}
        speedMultiplier={0.2}
        css={"background: red"}
      />
    </StyledDiv>
  );
}
