import React from "react";
import {
  IconTypography,
  IconTypographyMain,
  IconTypographySecondary,
} from "./styles";
import { Box, Stack } from "@mui/material";

export default function WebsiteIcon() {
  return (
    <Box display="flex" flexGrow={1}>
      <IconTypography>ES</IconTypography>
      <Stack>
        <IconTypographyMain>Elias Scharlach</IconTypographyMain>
        <IconTypographySecondary>Software Developer</IconTypographySecondary>
      </Stack>
    </Box>
  );
}
