import * as React from "react";
import { useState, useEffect } from "react";
import { CssBaseline, Toolbar, Button } from "@mui/material";
import { ThemeProvider } from "@mui/system";
import { darkTheme, lightTheme } from "./themes";
import NavBar from "./Components/NavBar";
import Footer from "./Components/Footer";
import { LandingSection } from "./Container/LandingSection";
import ScrollTo from "./Components/ScrollTo";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import { AboutSection } from "./Container/AboutSection";
// import { useWindowSize } from "./Components/useWindowSize";
// import { DevelopmentSection } from "./Container/DevelopmentSection";
import Loader from "./Components/Loader";

// const sections = ["Home", "About", "Development"];
const sections = ["Home", "About"];

function App() {
  // const [height, width] = useWindowSize();
  const [theme, setTheme] = useState("dark");
  const [loading, setLoading] = useState(false);

  const themeToggler = () => {
    theme === "light" ? setTheme("dark") : setTheme("light");
  };

  useEffect(() => {
    setLoading(true);
    window.addEventListener("load", () => {
      //site has been loaded
      setLoading(false);
    });
  }, []);

  return (
    <ThemeProvider theme={theme === "light" ? lightTheme : darkTheme}>
      <CssBaseline />
      {/* <p>
        _____________________________________________________ {height}, {width}
      </p> */}
      {loading ? (
        <Loader loading={loading} />
      ) : (
        <>
          <NavBar sections={sections} themeToggler={themeToggler}></NavBar>
          <Toolbar id="back-to-top-anchor" />
          <main>
            <LandingSection id="Home" />
            <AboutSection id="About" />
            {/* <DevelopmentSection id="Development" /> */}
            <ScrollTo scrollingId="Home">
              <Button size="small" aria-label="go to top">
                <KeyboardArrowUpIcon />
              </Button>
            </ScrollTo>
          </main>
          {/* <p>
    {height}, {width}
  </p> */}
          <footer>
            <Footer />
          </footer>
        </>
      )}
    </ThemeProvider>
  );
}

export default App;
