import { styled } from "@mui/system";
import { Box, Stack, Typography, Button } from "@mui/material";
import { fadeIn } from "react-animations";
import { keyframes } from "@mui/material";
export const fadeInAnimtion = keyframes`${fadeIn}`;

export const LandingSectionDiv = styled("div")(({ theme }) => ({
  // backgroundColor: theme.palette.secondary.main,
  scrollSnapAlign: "start",
  // borderBottom: "1px solid green", //TODO remove
  position: "relative",

  marginTop: "-64px", //equals to height of appbar
  [theme.breakpoints.down("sm")]: {
    marginTop: "-56px",
  },

  height: "100vh", //height of viewport - exception vh>900px
}));

export const StyledBackgroundText = styled(Typography)(({ theme }) => ({
  zIndex: 0,
  position: "absolute",
  top: "30%",
  left: "50%",
  transform: "translate(-50%, -50%)",

  fontWeight: "bold",
  fontSize: "20vw",
  color: "rgba(255, 255, 255, 0.01)",
  // color: "#171717", //TODO

  [theme.breakpoints.down("lg")]: {
    display: "none",
  },
}));

export const ImageBox = styled(Box)(({ theme }) => ({
  zIndex: 1,
  position: "absolute",
  bottom: "0%",
  right: "5%",
  height: "90vh",
  maxHeight: "90vh",
  filter: "grayscale(100%) contrast(170%)",

  [theme.breakpoints.down("sm")]: {
    height: "400px",
  },
  [theme.breakpoints.between("sm", "md")]: {
    height: "700px",
  },
  [theme.breakpoints.between("md", "lg")]: {
    height: "700px",
  },

  "@media(max-width: 380px)": {
    height: "350px",
  },

  "@media(max-width: 340px)": {
    height: "300px",
  },
  "@media(max-width: 300px)": {
    height: "250px",
  },

  animation: `${fadeInAnimtion} 2s linear 0s`,
}));

export const InformationStack = styled(Stack)(({ theme }) => ({
  zIndex: 5,
  position: "absolute",
  top: "30%",
  left: "10%",
  wordWrap: "break-word",

  [theme.breakpoints.down("sm")]: {
    top: "10%",
  },
  animation: `${fadeInAnimtion} 2s linear 0s`,
}));

export const TypographyTitle = styled(Typography)(({ theme, bold }) => ({
  fontWeight: bold ? "bold" : "",
  display: "inline-block",
  wordWrap: "break-word",
}));

export const TypographyDescription = styled(Typography)(({ theme }) => ({
  width: "65%",
  [theme.breakpoints.down("sm")]: {
    width: "100%",
  },
  [theme.breakpoints.between("sm", "md")]: {
    width: "85%",
  },
}));

export const ScrollLinkText = styled(Typography)(({ theme }) => ({
  fontWeight: "bold",

  "&:hover": {
    cursor: "pointer",
    textDecoration: "underline",
    transition: "all .2s ease-in-out",
    transform: "scale(1.1)",
  },
}));

export const SocialIconLink = styled(Button)(({ theme }) => ({
  height: "60px",
  width: "60px",
  fontSize: "30px",

  // color: theme.palette.text.primary,
  // color: "red",
  // borderRadius: "50%",
  // textAlign: "center",
  // backgroundColor: "#fff",
  // padding: "5px",
  // border: "2px solid white",

  "&:hover": {
    cursor: "pointer",
    // backgroundColor: theme.palette.primary.main,
    transition: "all .2s ease-in-out",
    transform: "scale(1.4)",
  },
}));
